<!-- EditProductForm.vue -->
<template>
  <VaForm ref="form" @submit.prevent="onSave">
    <VaInput
      v-model="formData.product_serial"
      :rules="[(v: string) => !!v || 'äº§å“åºåˆ—å·ä¸èƒ½ä¸ºç©º']"
      label="äº§å“åºåˆ—å·"
      class="mb-4"
    />
    <VaInput
      v-model="formData.product_name"
      :rules="[(v: string) => !!v || 'äº§å“åç§°ä¸èƒ½ä¸ºç©º']"
      label="äº§å“åç§°"
      class="mb-4"
    />
    <VaSelect
      v-model="formData.status"
      :options="statusOptions"
      text-by="text"
      value-by="value"
      label="çŠ¶æ€"
      class="mb-4"
    />

    <div class="flex justify-end space-x-2">
      <VaButton preset="secondary" @click="$emit('close')">å–æ¶ˆ</VaButton>
      <VaButton type="submit" :loading="loading">
        {{ editingProduct ? 'æ›´æ–°äº§å“' : 'æ·»åŠ äº§å“' }}
      </VaButton>
    </div>
  </VaForm>
</template>

<script setup lang="ts">
import { ref, watch } from 'vue'
import { Product, EmptyProduct } from '../../../pages/projects/types'

interface Props {
  editingProduct?: Product | null
  loading?: boolean
}

interface Emits {
  (e: 'save', product: Omit<EmptyProduct, 'test_items'>): void
  (e: 'close'): void
}

const props = withDefaults(defineProps<Props>(), {
  editingProduct: null,
  loading: false,
})

const emit = defineEmits<Emits>()

const statusOptions = [
  { value: 'not_started', text: 'æœªå¼€å§‹' },
  { value: 'in_progress', text: 'è¿›è¡Œä¸­' },
  { value: 'completed', text: 'å·²å®Œæˆ' },
  { value: 'failed', text: 'å¤±è´¥' },
]

const formData = ref<Omit<EmptyProduct, 'test_items'>>({
  product_serial: '',
  product_name: '',
  material_id: '' as any,
  status: 'not_started',
})

// ç›‘å¬ç¼–è¾‘äº§å“çš„å˜åŒ–
watch(
  () => props.editingProduct,
  (newProduct) => {
    if (newProduct) {
      formData.value = {
        product_serial: newProduct.product_serial,
        product_name: newProduct.product_name,
        material_id: newProduct.material_id,
        status: newProduct.status,
      }
    } else {
      // é‡ç½®è¡¨å•
      formData.value = {
        product_serial: '',
        product_name: '',
        material_id: '' as any,
        status: 'not_started',
      }
    }
  },
  { immediate: true },
)

const onSave = () => {
  // åŸºæœ¬éªŒè¯
  if (!formData.value.product_serial.trim()) {
    return
  }
  if (!formData.value.product_name.trim()) {
    return
  }

  emit('save', { ...formData.value })
}
</script>
