  min-width: 240px; /* å¢åŠ æœ€å°å®½åº¦ä»¥é€‚åº”è‹±æ–‡ */
  flex-shrink: 0; /* é˜²æ­¢åœ¨å°å±å¹•ä¸Šè¢«å‹ç¼© */
}

.view-mode-toggle :deep(.va-button-toggle__option) {
  min-width: 120px; /* å¢åŠ å•ä¸ªæŒ‰é’®çš„æœ€å°å®½åº¦ */
  white-space: nowrap;
  padding: 0 16px; /* å¢åŠ å†…è¾¹è· */
  font-size: 0.875rem; /* ç¨å¾®è°ƒæ•´å­—ä½“å¤§å° */
}

.search-input {
  min-width: 250px; /* ç¡®ä¿æœç´¢æ¡†æœ‰è¶³å¤Ÿå®½åº¦ */
  max-width: 300px;
}

/* å“åº”å¼è®¾è®¡ */
@media (min-width: 768px) {
  .view-mode-toggle {
    min-width: 280px; /* åœ¨è¾ƒå¤§å±å¹•ä¸Šæä¾›æ›´å¤šå®½åº¦ */
  }

  .view-mode-toggle :deep(.va-button-toggle__option) {
    min-width: 140px;
    padding: 0 20px;
  }

  .search-input {
    min-width: 300px;
    max-width: 350px;
  }
}

/* ç§»åŠ¨ç«¯ä¼˜åŒ– */
@media (max-width: 767px) {
  .view-mode-toggle {
    width: 100%;
    max-width: 100%;
  }

  .search-input {
    width: 100%;
    min-width: auto;
    max-width: none;
  }
}
</style>
